import React from "react";
import { Route, Switch , Redirect} from "react-router-dom";
import HomePage from "./home/HomePage";
import AboutPage from "./about/AboutPage";
import Header from "./common/Header";
import Contact from "./contact/contact"
import CoursesPage from "./courses/CoursesPage";
import { ToastContainer, toast } from 'react-toastify';
import "react-toastify/dist/ReactToastify.css";
import Work from "./work/work";
import Skills from "./skills/skills.js"


function App() {

    const notify = async (status, message) =>  {

        if (status === 200) {
            toast.success(message, {
                autoClose: 3000,
            });
        }

        if (status === 100) {
            toast.info(message, {
                autoClose: 2000,
            });
        }

        if (status === 400) {
            toast.error(message, {
                autoClose: 3000
            });
        }

        if (status === 500) {
            toast.error(message, {
                autoClose: 3000
            });
        }

    }

    //const [open, setOpen] = useState(false)
    //https://drive.google.com/file/d/1xiogGFaEAEllR5sIChM_A77t5g9_5LAx/view?usp=sharing
    return (
    <div className="container-fluid">
      <ToastContainer hideProgressBar />
      <Header />
      <Switch>
        <Route exact path="/" component={HomePage} />
        <Route path="/about" component={AboutPage} />
        <Route path="/skills" component={Skills} />
        <Route path="/contact" render={
          (props) => <Contact {...props} notify={notify} />
        } />
        {/*<Route path="/contact" component={Contact} />*/}
        <Route path="/resume" component={() => { window.location = process.env.REACT_APP_RESUME_GOOGLE_DRIVE_SHARED_LINK; return null;}} />
        <Route render={() => <Redirect to="/" />} />
      </Switch>
    </div>
    );
}

export default App;
