import React, {useState} from "react";
import './Contact.css'
import postData from '../../api/emailApi'
const re = /^(([^<>()\[\]\.,;:\s@\"]+(\.[^<>()\[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;

const Contact = (props) => {

    const [name, setName] = useState('');
    const [email, setEmail] = useState('');
    const [subject, setSubject] = useState('');
    const [text, setText] = useState('');
    const [textError, setTextError] = useState('');
    const [emailError, setEmailError] = useState('');
    const [nameError, setNameError] = useState('');
    const [subjectError, setSubjectError] = useState('');
    const error = "1px solid red"

    const handleSubmit = async () => {
        try {

            props.notify(100, 'Please wait! processing')

            setEmailError(email.length && re.test(email) ? '' : error)
            setTextError(text.length ? '' : error)

            if(!email.length)
                return props.notify(400, 'Please enter the email.')
            else if (!text.length)
                return props.notify(400, 'Please enter the message.')
            else if(!re.test(email)) {
                return props.notify(400,'Email is invalid');
            }

            let data = await postData({name: name, email: email, subject: subject, message: text})

            if (data.statusCode === 200) {
                props.notify(200, data.message);
            } else {
                props.notify(400, data.message);
            }

        } catch (error) {
            //console.log("login error: ", error)
            return props.notify(500, 'No response from server.')
        }

    }

    return (
        <div className='vertical-center-contact text-color'>
            <div className='vertical-items-about'>
                <div className='big-font-size-contact'>
                    <b>Let&apos;s talk.</b>
                </div>
                <br/>
                <div className='small-font-size-about grid-about'>
                    <div>
                        Get in touch via the form below, or by emailing <span
                        className='about-link'>qmohitsingh@gmail.com</span>.
                    </div>
                </div>
                <br/>
                <div>
                    <form>
                        <div className="form-group">
                            <label htmlFor="formGroupExampleInput">Name:</label>
                            <input
                                type="text"
                                className="form-control input-contact"
                                id="formGroupExampleInput"
                                placeholder='Enter your name'
                                onChange={(event) => {
                                    setName(event.target.value)
                                }}
                                style={{border:"none", borderBottom: "1px solid lightgray"}}
                            />
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleInputEmail1">Email:</label>
                            <input
                                type="email"
                                className="form-control input-contact"
                                id="exampleInputEmail1"
                                aria-describedby="emailHelp"
                                placeholder='Enter your email'
                                onChange={(event) => {
                                    setEmail(event.target.value)
                                }}
                                style={{border:"none", borderBottom: "1px solid lightgray"}}
                            />
                            {/*<small id="emailHelp" className="form-text text-muted">Well never share your email with*/}
                            {/*    anyone else.</small>*/}
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleInputEmail1">Subject:</label>
                            <input
                                type="email"
                                className="form-control input-contact"
                                id="exampleInputEmail1"
                                aria-describedby="emailHelp"
                                placeholder='Subject'
                                onChange={(event) => {
                                    setSubject(event.target.value)
                                }}
                                style={{border:"none", borderBottom: "1px solid lightgray"}}
                            />
                            {/*<small id="emailHelp" className="form-text text-muted">Well never share your email with*/}
                            {/*    anyone else.</small>*/}
                        </div>
                        <div className="form-group">
                            <label htmlFor="exampleFormControlTextarea1">Message:</label>
                            <textarea
                                className="form-control input-contact"
                                id="exampleFormControlTextarea1"
                                rows="5"
                                placeholder='Enter your message'
                                style={{border:"none", borderBottom: "1px solid lightgray"}}
                                onChange={(event) => {
                                    setText(event.target.value)
                                }}
                            />
                        </div>

                        <input
                            style={{
                                color: 'white',
                                backgroundColor: 'rgba(27,27,27,.91)',
                                borderRadius: '2em',
                                border: 'none',
                                height: "2.5em",
                                width: "5em"
                            }}
                            className='submit-button'
                            type="button"
                            value="Submit"
                            onClick={handleSubmit}
                        />
                    </form>
                </div>

            </div>
        </div>
    )
};

export default Contact;